import DataSheetBase from './DataSheetBase.js';
import datasheet_people_row0_picture from './images/datasheet_people_row0_picture.png';
import datasheet_people_row1_picture from './images/datasheet_people_row1_picture.png';
import datasheet_people_row2_picture from './images/datasheet_people_row2_picture.png';
import datasheet_people_row3_picture from './images/datasheet_people_row3_picture.png';
import datasheet_people_row4_picture from './images/datasheet_people_row4_picture.png';
import datasheet_people_row5_picture from './images/datasheet_people_row5_picture.png';
import datasheet_people_row6_picture from './images/datasheet_people_row6_picture.png';

export default class DataSheet_people extends DataSheetBase {

  constructor(id, updateCb) {
    super(id, updateCb);
    this.requestedKeyPath = "";  // this value can be specified in the React Studio data sheet UI
  }

  makeDefaultItems() {
    // eslint-disable-next-line no-unused-vars
    let key = 1;
    // eslint-disable-next-line no-unused-vars
    let item;
    
    item = {};
    this.items.push(item);
    item['name'] = "Jon";
    item['about'] = "I’m an EdTech development manager. As a result I have contacts in over 100 higher education institutions and know the intricacies of their needs pertaining to EdTech. I’ve completed EdTech sales in over 40 universities with 100% success. With a strong background in web development and scientific programming I’m ready to change the world of education.";
    item['picture'] = datasheet_people_row0_picture;
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['name'] = "Rob";
    item['about'] = "I’m a PhD researcher in EdTech, with the subject of my research being the method diversity of student responses. I’ve created dozens of complex applications, including a JavaScript mathematics engine, and I have been a lead developer for critical add-ons used by a leading EdTech product world wide.";
    item['picture'] = datasheet_people_row1_picture;
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['name'] = "Manjinder";
    item['about'] = "I’ve been intricately involved in teaching for many years. I have over 6 years of private tutoring experience, most recently worked on designing and delivering crash courses for a Birmingham high school A-level class. I am combining this with over 7 years of programming experience, to design the perfect learning environment.";
    item['picture'] = datasheet_people_row2_picture;
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['name'] = "Austin";
    item['about'] = "I have had a passion for teaching for as long as I can remember. After finishing my PhD, where I won a PGTA scholarship to recognise excellent teaching quality at the university, I went on to complete a PGDipEd. I am a fully qualified teacher. With over 8 years experience in software development and high performance computing, I have a balance of teaching and software development experience. ";
    item['picture'] = datasheet_people_row3_picture;
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['name'] = "George";
    item['about'] = "I am a university level teaching assistant and assessed problem marker. I have been a question author supporting core teaching at UoB and have created over 250 questions. During my undergraduate degree I won the Moreton prize for outstanding academic achievement. I’m someone who is invested in teaching, question designing, and programming.";
    item['picture'] = datasheet_people_row4_picture;
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['name'] = "Rich";
    item['about'] = "I’m an EdTech developer at UoB creating learning material for universities around the world. With a PhD in theoretical physics, I bring a pure mathematical approach to traditionally numerically heavy areas where possible. I have industrial experience with machine learning, having developed algorithms for dozens of applications. I can’t wait to bring the power of machine learning to education.";
    item['picture'] = datasheet_people_row5_picture;
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['name'] = "Nicola";
    item['about'] = "I’m the Director of Education, College of Engineering & Physical Sciences at UoB. As a result, I have responsibility for the assessment requirements of 5,000 students. I’ve been recognised for teaching innovation. With over 100 contacts in higher education institutions across the world, I know the change 6 Bit will bring to education.";
    item['picture'] = datasheet_people_row6_picture;
    item.key = key++;
  }

}



